@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
body, html {
  margin: 0px;
  padding: 0px;
  overflow-x: hidden;
  font-family: 'Lato', sans-serif !important;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased !important;
  background-color: #FFF;
}

a{
  text-decoration: none;
}

html.touch *:hover {
  all:unset!important;
  pointer-events: none !important;
}
.navbar{
    width: 100vw;
    display: flex;
    position: absolute;
    overflow-y: hidden;
    z-index: 5;
}

.navbar-container{
    margin: auto auto;
    width: 80vw;
    height: 90px;
    display: flex;
}

.navbar-collapse{
    display: none;
}

.logo-desktop{
    margin: auto 0;
    width: 30px;
    height: 35px;
}
.logo-desktop:hover{
    -webkit-animation: shake 0.5s;
            animation: shake 0.5s; 
}
@-webkit-keyframes shake {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    33% { -webkit-transform: rotate(-10deg); transform: rotate(-10deg); }
    66% { -webkit-transform: rotate(10deg); transform: rotate(10deg); }
    100% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
}
@keyframes shake {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    33% { -webkit-transform: rotate(-10deg); transform: rotate(-10deg); }
    66% { -webkit-transform: rotate(10deg); transform: rotate(10deg); }
    100% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
}

.logo-mobile{
    display: none;
}

.links-container{
    margin: auto 0px auto auto;
    width: auto;
    height: auto;
    display: flex;
}
.links-container h2{
    padding: 20px;
    cursor: pointer;
    margin: auto auto;
    font-size: 15px;
    color: white;
    font-weight: normal;
    border: solid transparent;
    border-width: 0 0 4px 0;
    letter-spacing: 0.5px;
    -webkit-transition: all 0.1s ease;
    transition: all 0.1s ease;
}
.links-container h2:hover{
    border: solid white;
    border-width: 0 0 4px 0;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
}

.button {
    margin: auto 0px auto 20px;
    background-color: white;
    width: auto;
    height: auto;
    cursor: pointer;
    display: flex;  
    border-radius: 5px;
    -webkit-transition: all 0.25s ease;
    transition: all 0.25s ease;
}
.button:hover {
    background-color: #ffffff25;
    -webkit-transition: all 0.25s ease;
    transition: all 0.25s ease;
}
.button h2{
    margin: auto auto;
    font-size: 15px;
    color: #06B2DF;
    padding: 10px 15px;
    border: solid transparent;
    border-width: 2px;
    font-weight: normal;
    letter-spacing: 0.5px;
    -webkit-transition: all 0.25s ease;
    transition: all 0.25s ease;
}
.button h2:hover{
    color: white;
    border: solid white;
    border-width: 2px;
    border-radius: 5px;
    -webkit-transition: all 0.25s ease;
    transition: all 0.25s ease;
}

.menu{
    display: none;
}

.home-button{
    position: fixed;
    z-index: 11;
    width: 35px;
    height: 35px;
    background-color: #24292E;
    box-shadow: 0px 4px 7px rgba(0, 0, 0, 0.4);
    display: flex;
    border-radius: 100%;
    bottom: 25px;
    right: 25px;
    cursor: pointer;
}
.home-button .home-button-icon{
    margin: auto;
    font-size: 20px;    
    color: white;   
}
@media only screen and (max-width:500px){
    .navbar{
        width: 100vw;
        display: flex;
        position: relative;
        background-color: #ffffff;
        height: 80px;
    }
    
    .navbar-container{
        margin: auto auto auto auto;
        width: 85vw;
        height: 13vw;
        display: flex;
    }

    .navbar-collapse{
        position: relative;
        background-color: #0598E3;
        margin: 0 auto 0 auto;
        width: 80vw;
        height: auto;
        display: flex;
        flex-direction: column;
        box-shadow: 0px 5px 10px hsla(0, 0%, 0%, 0.3);
        padding: 10px 0;
    }

    .navbar-collapse hr{
        opacity: 0.2;
    }

    .navbar-collapse h1{
        text-align: center;
        font-size: 18px;
        margin: 0 0;
        width: 100%;
        padding: 5px 0;
        font-weight: normal;
        color: #ffffff;
    }
    
    .logo-desktop{
        display: none;
    }
    .logo-mobile{
        display: flex;
        margin: auto 0;
        width: 30px;
        height: 35px;
    }
    
    .links-container{
        display: none;
    }

    .button{
        display: none;
    }

    .menu{
        display: flex;
        margin: auto 0 auto auto;
        width: 25px;
        height: 25px;
    }
}
.home {
    display: flex;
    width: 100vw;
    height: 100vh;
    background-position: center; 
    background-size: cover;
    background-attachment: fixed;
    margin: 0px;
    overflow-y: hidden;
}

.home-container{
    margin: auto auto;
    height: auto; 
    width: 80vw; 
    display: flex;
}

.home-container-text{
    margin: auto auto auto 0px;
    height: auto;
    width: auto;
    display: inline-block;
}

.presentation{
    color: white; 
    font-size: 30px; 
    line-height: 45px;
    font-weight: 100;
    letter-spacing: 2px;
}
.presentation strong{
    font-weight: 900;
}

.summary{
    text-align: justify;
    color: white;
    font-size: 15px;
    line-height: 20px;
    width: 560px;
}
@media only screen and (max-width:500px){
    .home {
        display: flex;
        width: 100vw;
        height: 87vh;
        background-position: center; 
        background-size: cover;
        background-attachment: fixed;
    }
    
    .home-container{
        margin: auto auto;
        height: auto; 
        width: 85vw; 
        display: flex;
    }
    
    .home-container-text{
        margin: auto auto auto 0px;
        height: auto;
        display: inline-block;
    }
    
    .presentation{
        text-align: left;
        color: white; 
        font-size: 25px; 
        line-height: 35px;
        font-weight: lighter;
        letter-spacing: 2px;
    }
    
    .summary{
        text-align: justify;
        color: white;
        font-size: 13px;
        line-height: 20px;
        width: auto;
    }
}
.skills{
    width: 100vw;
    min-height: 100vh;
    margin: 0px;
    position: relative;
    overflow-x: hidden;
}

.skills-container{
    width: 80vw; 
    margin: auto auto;
}
.skills-container-title{
    text-align: left;
    font-size: 30px;
    font-weight: bold;
    margin-top: 0;
    color: #24292E;
    padding: 50px 0 50px 0;
}

.skills-box{
    display: flex;
    min-height: 200px;
    margin-bottom: 40px;
}
.skills-box-icon{
    width: 50px;
    height: 50px;
}
.skills-box-text{
    text-align: left;
    margin: 0 0 0 40px;
    line-height: 11px;
    font-size: 15px;
    color: #24292E;
}
.skills-box-text strong{
    font-size: 20px;
    line-height: 20px
}

.box{
    display: flex;
}
.box-container{
    width: 33%;
    height: auto;
    display: flex;
}
.box-container-left{
    margin: 0 auto 0 0;
}
.box-container-center{
    margin: auto auto;
}
.box-container-right{
    margin: 0 0 auto auto;
}
@media only screen and (max-width:500px){
    .skills{
        height: auto;
    }
    .skills-container{
        margin-bottom: 0;
    }
    .skills-box{
        display: flex;
        height: auto;
        min-height: auto;
        margin-bottom: 50px;
        width: 100vw;
    }
    .box{
        display: inline-block;
    }
}
.projects{
    width: 100vw;
    height: auto;
    margin: 0px;
    position: relative;
    overflow-x: hidden;
}
.projects-container{
    width: 80vw;
    height: auto; 
    margin: auto auto;
    margin-bottom: 50px
}
.projects-container-title{
    text-align: left;
    font-size: 30px;
    font-weight: bold;
    margin-top: 0;
    color: #24292E;
    padding: 50px 0 50px 0;
}

.mockup-grid{
    display: grid;
    grid-template-columns: auto auto;
    grid-gap: 30px;
}
.mockup-image{
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    width: 100%;
    height: 300px;
    background-position: center;
    background-size: cover;
    background-attachment: scroll;
    cursor: pointer;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
    margin: 0;
}
.mockup-description{
    width: 100%;
    height: auto;
    background-color: white; 
    margin: 0;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
}
.mockup-description h1 {
    font-size: 22px;
    margin-top: 25px;
    padding-left: 25px;
    padding-right: 25px;
    margin-bottom: 5px;
    font-weight: bolder;
    color: #555555;
}
.mockup-description h2 {
    font-size: 15px;
    padding-left: 25px;
    padding-right: 25px;
    margin-bottom: 5px;
    font-weight: normal;
    color: #AAAAAA;
}
.mockup-description p {
    font-size: 17px;
    padding-left: 25px;
    padding-right: 25px;
    font-weight: lighter;
    color: #555555;
    text-align: justify;
}
.mockup-description a {
    margin-top: 25px;
    padding-left: 25px;
    padding-right: 25px;
    margin-bottom: 25px;
    width: 120px;
    height: 26px;
    font-weight: bold;
    align-items: center;
    display: flex; 
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
    color: #555555;
}
.mockup-description a:hover{
    color: black !important;
    -webkit-transition: all 0.5s ease !important;
    transition: all 0.5s ease !important;
}
.mockup-description a span {
    margin: auto 10px auto 0;
}

.right {
    margin: 40px 0 40px 40px !important;
}
.left {
    margin: 40px 40px 40px 0 !important;
}

.card{
    width: 37vw;
    margin-bottom: 50px;
    box-shadow: 0px 10px 40px rgba(0, 0, 0, 0.2);
    border-radius: 10px;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
    -webkit-filter: grayscale(100%);
    filter: grayscale(100%);
    opacity: 0.5;
}
.card:hover{
    box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.3);
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
    -webkit-transform: translate(0, -15px);
            transform: translate(0, -15px);
    -webkit-filter: grayscale(0%);
    filter: grayscale(0%);
    opacity: 1;
}

.blue-button {
    height: auto;
    display: flex;
    border-radius: 5px;
    margin: 15px auto 90px auto;
    background-color: #ffffff25;
    -webkit-transition: all 0.25s ease;
    transition: all 0.25s ease;
}
.blue-button:hover {
    background-color: #ffffff25;
    -webkit-transition: all 0.25s ease;
    transition: all 0.25s ease;
}
.blue-button h2{
    background-color: #0598E3;
    margin: auto auto;
    font-size: 15px;
    color: #ffffff;
    padding: 8px 25px;
    border: solid #0598E3;
    border-width: 2px;
    border-radius: 5px;
    font-weight: normal;
    letter-spacing: 0.5px;
    cursor: pointer;
    -webkit-transition: all 0.25s ease;
    transition: all 0.25s ease;
}
.blue-button h2:hover{
    color: #0598E3;
    border: solid #0598E3;
    background-color: #ffffff;
    border-width: 2px;
    border-radius: 5px;
    -webkit-transition: all 0.25s ease;
    transition: all 0.25s ease;
}
@media only screen and (max-width:500px){
    .mockup-grid{
        grid-template-columns: auto;
    }
    .mockup-image{
        height: 60vw;
    }
    .card{
        margin: 40px 40px 20px 0 !important;
        box-shadow: 0px 10px 50px rgba(0, 0, 0, 0.2);
        -webkit-filter: grayscale(0%);
        filter: grayscale(0%);
        opacity: 1;
        width: 80vw;
    }
    .card:hover{
        -webkit-transform: translate(0, 0);
                transform: translate(0, 0);
    }
    .mockup-description h1 {
        font-size: 18px;
    }
    .mockup-description h2 {
        font-size: 12px;
    }
    .mockup-description p {
        font-size: 15px;
    }
    .mockup-description a {
        font-size: 14px;
    }
    .blue-button h2:hover{
        background-color: #0598E3;
        font-size: 15px;
        color: #ffffff;
        padding: 8px 25px;
        border: solid #0598E3;
        border-width: 2px;
        border-radius: 5px;
        font-weight: normal;
        letter-spacing: 0.5px;
        cursor: not-allowed;
        -webkit-transition: all 0.25s ease;
        transition: all 0.25s ease;
    }
}
.contact {
    width: 100vw;
    height: 100vh;
    background-position: center; 
    background-size: cover;
    background-attachment: fixed;
    margin: 0px;
    overflow-y: hidden;
    overflow-x: hidden;
}
.contact-container{
    width: 80vw;
    height: auto; 
    margin: auto auto;
}
.contact-container-title{
    text-align: left;
    font-size: 30px;
    font-weight: bold;
    margin-top: 0;
    color: #ffffff;
    padding: 50px 0 50px 0;
}

.contact-form{
    display: flex;
}
.form-input{
    width: 38vw;
    height: 50vh;
    margin: 0 auto 90px 0;
}
.form-input input{
    background-color: white;
    font-size: 13px;
    border-width: 0;
    height: 32px; 
    width: 80%; 
    border-radius: 5px;
    box-shadow: 0 5px 5px rgba(0, 0, 0, 0.1);
    margin-bottom: 10px;
    padding-left: 15px;
    color: #24292E;
}

.form-textarea{
    width: 38vw;
    height: 50vh;
    margin: 0 0 90px auto;
}
.form-textarea textarea{
    background-color: white;
    font-size: 13px;
    border-width: 0;
    height: 250px; 
    width: 100%; 
    border-radius: 5px;
    box-shadow: 0 5px 5px rgba(0, 0, 0, 0.1);
    margin-bottom: 10px;
    padding-left: 15px;
    padding-top: 10px;
    color: #24292E;
    resize: none;
}
.form-textarea button{
    background-color: #24292E;
    border-width: 2px !important;
    border: solid #24292E;
    color: white;
    font-size: 15px;
    padding: 8px 13px;
    border-radius: 5px;
    cursor: pointer;
    -webkit-transition: all 0.25s ease;
    transition: all 0.25s ease;
}
.form-textarea button:hover{
    background-color: #ffffff;
    color: #24292E;
    font-size: 15px;
    border-radius: 5px;
    -webkit-transition: all 0.25s ease;
    transition: all 0.25s ease;
}

input::-webkit-input-placeholder, textarea::-webkit-input-placeholder{
    opacity: 0.7;
    font-weight: normal;
    font-size: 13px;
    letter-spacing: 0px;
    font-family: 'Lato', sans-serif;
}

input::-moz-placeholder, textarea::-moz-placeholder{
    opacity: 0.7;
    font-weight: normal;
    font-size: 13px;
    letter-spacing: 0px;
    font-family: 'Lato', sans-serif;
}

input:-ms-input-placeholder, textarea:-ms-input-placeholder{
    opacity: 0.7;
    font-weight: normal;
    font-size: 13px;
    letter-spacing: 0px;
    font-family: 'Lato', sans-serif;
}

input::-ms-input-placeholder, textarea::-ms-input-placeholder{
    opacity: 0.7;
    font-weight: normal;
    font-size: 13px;
    letter-spacing: 0px;
    font-family: 'Lato', sans-serif;
}

input::placeholder, textarea::placeholder{
    opacity: 0.7;
    font-weight: normal;
    font-size: 13px;
    letter-spacing: 0px;
    font-family: 'Lato', sans-serif;
}
input:focus, textarea:focus{
    outline: none;
}

.contact-footer{
    display: flex;
    margin-bottom: 30px;
}
.footer-description{
    margin: auto auto 0 0;
    text-align: justify;
    color: white;
    font-size: 15px;
    line-height: 20px;
    font-weight: lighter;
    letter-spacing: 1px;
}
.footer-social{
    margin: auto 0 0 15px;
    width: auto;
    height: 22px;
    opacity: 0.6;
    -webkit-transition: all 0.25s ease;
    transition: all 0.25s ease;
}
.footer-social:hover{
    opacity: 1;
    -webkit-transition: all 0.25s ease;
    transition: all 0.25s ease;
}
@media only screen and (max-width:500px){
    .contact{
        height: auto;
    }
    .contact-container{
        display: 100vw;
    }
    .contact-form{
        display: inline;
    }
    .form-input{
        width: 95vw;
        height: 43px;
    }
    .form-textarea{
        width: 75vw;
        display: inline-block;
        margin: 0 0 150px 0;
    }
    .contact-footer{
        display: inline-block;
        margin-bottom: 0;
    }
    .footer-description{
        font-size: 13px;
    }
    .footer-social{
        margin: 15px 15px 25px 0;
        opacity: 1;
        height: 20px;
    }
    .form-textarea button:hover{
        background-color: #24292E;
        border-width: 2px !important;
        border: solid #24292E;
        color: white;
        font-size: 15px;
        padding: 8px 13px;
        border-radius: 5px;
        cursor: not-allowed;
        -webkit-transition: all 0.25s ease;
        transition: all 0.25s ease;
    }
}
